export default {
	data() {
		return {
			moldIndex: '0',
			moldArray: ['工单号', '姓名', '联系电话'],
			typeIndex: ' ',
			typeArray: ['承保类', '待处理报案', '电销类', '救援反馈'],
			date: ' ',
		}
	},
	onLoad() {

	},
	methods: {
		// 选择输入框的字段名称
		bindPickerChange(e) {
			this.moldIndex = e.detail.value
		},

		// 工单类型选项
		bindPickerType(e) {
			this.typeIndex = e.detail.value
		},
		
		// 选择日期
		bindDateChange(e) {
			this.date = e.detail.value
		}
	}
}
